import store from '@/store'
import axios from 'axios'
import { Message } from 'element-ui'
import util from '@/libs/util'

// 
function errorCreat (msg) {
  const err = new Error(msg)
  errorLog(err)
  throw err
}

// 
function errorLog (err) {
  // 
  store.dispatch('d2admin/log/add', {
    type: 'error',
    err,
    info: ''
  })
  // 
  if (process.env.NODE_ENV === 'development') {
    util.log.danger('>>>>>> Error >>>>>>')
    console.log(err)
  }
  // 
  Message({
    message: err.message,
    type: 'error',
    duration: 5 * 1000
  })
}

//  axios 
const service = axios.create({
  baseURL: process.env.VUE_APP_API,
  timeout: 5000 // 
})

// 
service.interceptors.request.use(
  config => {
    // 
    // if (!(/^https:\/\/|http:\/\//.test(config.url))) {
    //   const token = util.cookies.get('token')
    //   if (token && token !== 'undefined') {
    //     // token-- ['X-Token']key 
    //     config.headers['X-Token'] = token
    //   }
    // }

    return config
  },
  error => {
    // 
    console.log(error)
    Promise.reject(error)
  }
)

// 
service.interceptors.response.use(
  response => {
    // dataAxios  axios  data
    const dataAxios = response.data
    // alert(JSON.stringify(dataAxios))
    // 
    const { code } = dataAxios
    //  code 
    if (code === undefined) {
      //  code   D2Admin 
      return dataAxios
    } else {
      //  code  
      switch (code) {
        case 0:
          return dataAxios
        case 401:
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
        case 403:
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
        case 404:
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
        case -1:
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
        case -1001:
        case -5001:
          // 
          const host = window.location.host
          // window.location.href = host + '/#/'
          window.location.assign(host)
          // alert(res.data.message)
          // alert(window.location.host)
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
        case -5003:
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
        case -5004:
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
        case -5005:
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
        default:
          errorCreat(`${code}: ${dataAxios.message}-${response.config.url}`)
          break
      }
    }
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400: error.message = ''; break
        case 401: error.message = ''; break
        case 403: error.message = ''; break
        case 404: error.message = `: ${error.response.config.url}`; break
        case 408: error.message = ''; break
        case 500: error.message = ''; break
        case 501: error.message = ''; break
        case 502: error.message = ''; break
        case 503: error.message = ''; break
        case 504: error.message = ''; break
        case 505: error.message = 'HTTP'; break
        default: break
      }
    }
    errorLog(error)
    return Promise.reject(error)
  }
)

export default service
